<template>
    <div>
        <Title :title="title" back="true" exim="true" />

        <b-container fluid class="p-4">
            <b-row>
                <b-col cols="12" xl="8">
                    <!-- search bar -->
                    <serch-bar @filterChange="onFilterChange" :filter="searchFilter" />
                    <!-- filters -->
                    <Filters
                        v-if="filters.model.length != 0"
                        :filtersData="filters"
                        @applyFilters="onApplyFilters"
                        @resetFilters="onResetFilters"
                    />
                    <!-- table -->
                    <Table
                        :items="items"
                        :fields="fields"
                        :fieldsModels="fieldsModels"
                        :actionList="actionsList"
                        :filter="searchFilter"
                        :isBusy="isBusy"
                        :perPage="perPage"
                        :currentPage="currentPage"
                        :name="name"
                    />
                    <!-- pagination -->
                    <Pagination
                        :rows="rows"
                        :perPage="perPage"
                        :currentPage="currentPage"
                        :fields="fields"
                        :fieldsModels="fieldsModels"
                        @pageChange="onPageChange"
                        @addNewRow="onAddNewRow"
                    />
                </b-col>
            </b-row>
        </b-container>
    </div>
</template>

<script>
/*eslint no-unused-vars: ["error", { "argsIgnorePattern": "^_" }]*/
import API from "../api/ApiUtils.js";
import Title from "@/components/Title.vue";
import SerchBar from "../components/tables/SerchBar.vue";
import Filters from "../components/tables/Filters.vue";
import Table from "@/components/tables/Table.vue";
import Pagination from "../components/tables/Pagination.vue";

export default {
    components: { Title, SerchBar, Filters, Table, Pagination },
    props: ["name", "id", "parent"],
    data() {
        return {
            API: new API(this),
            title: "",
            searchFilter: null,
            isBusy: false,
            perPage: 10,
            currentPage: 1,
            filters: {
                model: [],
                list: {},
                state: {},
            },

            items: [],
            fields: [],
            fieldsModels: {},
            isFiltered: false,
            actionsList: [],
        };
    },
    methods: {
        getTableInfo() {
            this.filters.model = [];
            this.filters.state = {};

            this.actionsList = [];
            this.API.getTableInfo(this.name).then((data) => {
                let tableInfo = data;
                this.title = tableInfo.title;
                this.fields = tableInfo.fields;

                if (tableInfo.filters.length > 0) {
                    this.filters.model = tableInfo.filters;
                    tableInfo.filters.forEach((filter, i) => {
                        this.$set(this.filters.state, filter.key, "");
                        this.$set(this.filters.model[i], "disabled", i == 0 ? false : true);
                    });
                }

                if (tableInfo.actions.length > 0) {
                    this.actionsList = tableInfo.actions;
                }
                this.fields.push({ key: "actions", label: "Действия" });
                this.actionsList.push({ action: "delete" });

                this.getFieldsModels();
                this.getFiltersItems();
            });
        },
        getItems(params) {
            this.isBusy = true;
            this.API.getData(this.name, params).then((data) => {
                this.items = data;
                this.isBusy = false;
            });
        },
        getFieldsModels() {
            this.fieldsModels = {};
            this.selectableFields.forEach((field) => {
                this.API.getData(field.key).then((data) => {
                    this.$set(this.fieldsModels, field.key, data);
                });
            });
        },
        getFiltersItems() {
            this.filters.list = {};
            this.filters.model.forEach((filter) => {
                this.API.getData(filter.key).then((data) => {
                    this.$set(this.filters.list, filter.key, data);
                });
            });
        },
        onApplyFilters({ _id, fields }) {
            let current = this.filters.model.findIndex((x) => x.key == fields.field);
            if (current != this.filters.model.length - 1) {
                this.$set(this.filters.model[current + 1], "disabled", false);
            }

            this.isFiltered = true;
            this.$set(this.filters.state, fields.field, fields.value);
            this.getItems(this.filterParams);
        },
        onResetFilters(id) {
            for (let i = id; i < this.filters.model.length; i++) {
                this.$set(this.filters.model[i], "disabled", i == 0 ? false : true);
            }
            this.getItems(this.filterParams);

            if (id == 0) this.isFiltered = false;
        },
        onFilterChange(newValue) {
            this.searchFilter = newValue;
        },
        onPageChange(newCurrentPage) {
            this.currentPage = newCurrentPage;
        },
        onAddNewRow(item) {
            console.log("emit add", item);
            this.API.addRow(this.name, item).then((data) => {
                if (this.isFiltered) {
                    this.getItems(this.filterParams);
                }
                this.items.push(data[0]);
            });
        },
        init() {
            this.isFiltered = false;
            this.getTableInfo();
            this.getItems();
            let params = {};
            if (this?.parent) {
                params = {
                    [this?.parent]: this?.id,
                };
            }
            this.getItems(params);
        },
    },
    computed: {
        rows() {
            return this.items.length;
        },
        selectableFields() {
            let a = [];
            this.fields.forEach((field) => {
                if (Object.prototype.hasOwnProperty.call(field, "type")) {
                    if (field.type == "selectable") {
                        a.push(field);
                    }
                }
            });
            return a;
        },
        filterParams() {
            let params = {
                mode: "advanced_filters",
            };
            for (let filter in this.filters.state) {
                params[filter] = this.filters.state[filter];
            }
            return params;
        },
    },
    mounted() {
        this.init();
    },
    watch: {
        name: function () {
            this.init();
        },
    },
};
</script>
