<template>
    <b-row>
        <b-col>
            <b-pagination
                v-model="currentPageLocal"
                :total-rows="rows"
                :per-page="perPage"
                first-number
                last-number
                aria-controls="table"
            ></b-pagination>
        </b-col>
        <b-col cols="3" class="text-end">
            <add-new-row
                @addNewRow="onAddNewRow"
                :fields="fields"
                :fieldsModels="fieldsModels"
                title="Добавление записи"
            />
        </b-col>
    </b-row>
</template>

<script>
import AddNewRow from "./AddNewRow.vue";

export default {
    components: { AddNewRow },
    props: ["rows", "perPage", "currentPage", "fields", "fieldsModels"],
    data() {
        return {
            currentPageLocal: this.currentPage,
        };
    },
    methods: {
        onAddNewRow(fields) {
            console.log("pag emit add", fields);
            this.$emit("addNewRow", fields);
        },
    },
    watch: {
        currentPage: function (newCurrentPage) {
            this.currentPageLocal = newCurrentPage;
        },
        currentPageLocal: function (newCurrentPage) {
            this.$emit("pageChange", newCurrentPage);
        },
    },
};
</script>
